<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="120px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="合同书名">
              <el-input v-model="setting.title" size="small" placeholder="请输入内容"></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="乙方姓名">
              <el-input v-model="setting.name2" size="small" placeholder="请输入内容"></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="丙方姓名">
              <el-input v-model="setting.name3" size="small" placeholder="请输入内容"></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="合同内容">
              <el-input
                size="small"
                :rows="10"
                :cols="20"
                type="textarea"
                v-model="setting.contract_content"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="乙方印章">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="fileSuccess1"
                :before-upload="fileSuccessBefore"
                :file-list="fileList1"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽500px 高:500px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="丙方印章">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="fileSuccess2"
                :before-upload="fileSuccessBefore"
                :file-list="fileList2"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽500px 高:500px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import fetch2 from '../../utils/fetch2'

export default {
  data() {
    return {
      myHeaders: { Authorization: 'Bearer '+localStorage.getItem('pstt')},
      loading: false,
      fileList1: [],
      fileList2: [],
      setting: {},
    }
  },
  mounted() {
    this.loading = true
    fetch2('/sys/config/infocontractdq')
      .then(r => {
        this.setting = r.data
        this.loading = false
        this.fileList1 = [
          {
            name: r.data.contract_image1,
            url: r.data.contract_image1,
          },
        ]
        this.fileList2 = [
          {
            name: r.data.contract_image2,
            url: r.data.contract_image2,
          },
        ]
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },

    onSubmit() {
      this.loading = true
      Fetch('/sys/config/update', {
        type: 'contract_dq',
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    fileSuccess1(res) {
      this.setting.contract_image1 = res.data.file
    },
    fileSuccess2(res) {
      this.setting.contract_image2 = res.data.file
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
